:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --primary: hsl(217, 19%, 35%);
  --secondary: hsl(214, 17%, 51%);
  --accent: hsl(212, 23%, 69%);
  --neutral: hsl(210, 46%, 95%);
}

:root {
  --font-body: "Manrope", sans-serif;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
body {
  font-family: "Manrope", sans-serif;
  background-color: var(--neutral);
}

.container {
  height: 100vh;
  display: grid;
  place-content: center;
}

.hidden {
  display: none;
}

.card {
  background-color: var(--white);
  border-radius: 0.6875rem;
  box-shadow: var(--primary) 0px 10px 15px -10px;
  overflow: hidden;
}
.card-img {
  width: 100%;
  height: 12.5rem;
  background: url(/dist/images/drawers.jpg) no-repeat;
  background-size: 100%;
  border-radius: 0.6875rem 0.6875rem 0 0;
}
.card-content {
  padding: 2.375rem 2rem 1.25rem;
}
.card-title {
  color: var(--primary);
  font-weight: 700;
  margin-bottom: 0.875rem;
  line-height: 1.5;
  letter-spacing: 0.015rem;
}
.card-text {
  font-size: 0.8125rem;
  color: var(--accent);
  letter-spacing: 0.01rem;
  line-height: 1.5;
  margin-bottom: 1.375rem;
}
.card .profile {
  display: flex;
  position: relative;
  align-items: center;
  gap: 1rem;
  padding-top: 0.625rem;
}
.card .profile-img {
  width: 2.5rem;
  border-radius: 50%;
  overflow: hidden;
}
.card .profile-name {
  font-size: 0.8125rem;
  color: var(--primary);
  font-weight: 700;
  letter-spacing: 0.01rem;
}
.card .profile-date {
  font-size: 0.8125rem;
  color: var(--accent);
}
.card .profile .share {
  padding: 0.5rem 0.4375rem;
  border-radius: 50%;
  outline: none;
  border: none;
  background-color: var(--neutral);
  margin-left: auto;
  cursor: pointer;
  z-index: 1;
}
.card .profile .share:active, .card .profile .share:hover {
  background-color: var(--secondary);
  color: var(--white);
}
.card .profile .overlay {
  position: absolute;
  background-color: var(--primary);
  width: 150%;
  height: 150%;
  top: 0;
  left: -32px;
  right: 0;
  display: flex;
  align-items: center;
  color: var(--white);
  gap: 1rem;
  transition: 0.5s ease-in-out;
  display: none;
  padding-bottom: 0.625rem;
}
.card .profile .overlay span {
  padding-left: 2rem;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  font-size: 0.8125rem;
}
.card .profile .overlay i {
  font-size: 1.25rem;
  cursor: pointer;
}
@media (min-width: 40em) {
  .card {
    max-width: 45.625rem;
    display: grid;
    grid-template-columns: 39.2% 61%;
    overflow: visible;
  }
  .card-img {
    height: 100%;
    background-size: 140%;
    border-radius: 0.6875rem 0 0 0.6875rem;
  }
  .card-content {
    padding: 2.375rem 2.5rem 2rem;
  }
  .card-title {
    font-size: 1.25rem;
    line-height: 1.3;
  }
  .card-text {
    margin-bottom: 0.625rem;
  }
  .card .profile .overlay {
    max-width: 15.5rem;
    max-height: 3.4375rem;
    padding: 0;
    top: -3.125rem;
    right: 0;
    left: 14.0625rem;
    border-radius: 0.625rem;
  }
  .card .profile .overlay::after {
    content: " ";
    position: absolute;
    top: 100%; /* At the bottom of the tooltip */
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: var(--primary) transparent transparent transparent;
  }
}